.form-field {
  display: block;
  width: 100%;
  height: 48px;
  border: 2px solid transparent;
  border-radius: 8px;
  padding: 14px 16px;
  color: $colorBlack;
  background-color: #f7f8ff;

  &::placeholder {
    font-size: 14px;
    line-height: 20px;
    color: #8f99a8;
  }

  @include hover(border-color, #ccd0e0);
  
  &:focus {
    border-color: $colorBlue;
    outline: none;
  }

  &--textarea {
    height: 144px;
    font-family: inherit;
    resize: none;
  }
}
